body {
  background: #031625;
  font-family: "Noto Sans TC", sans-serif;
  position: relative;
  background: #020209;
  background: linear-gradient(90deg, #020209 0%, #060526 48%, #121244 63%, #090a4f 73%, #332408 96%, #392807 100%);
}

hr {
  margin-top: 23vh;
  margin-bottom: 8vh;
  width: 50vw;
  border: 2px solid #e8d5b5;
}

img {
  border-radius: 4px;
}

h3 {
  color: #e8d5b5;
  font-size: 50px;
  text-align: center;
  margin-bottom: 5vh;
  margin-top: 5vh;
}

#nav-bar {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  z-index: 100;
  display: flex;
  height: 10vh;
  width: 100vw;
  justify-content: space-between;
  align-items: center;
  background-color: #153464;
}

#nav-bar h1 {
  font-size: 3em;
  color: #e8d5b5;
  margin-left: 2vh;
}

#nav-bar ul {
  display: flex;
  flex-grow: 1.5;
  justify-content: flex-end;
  font-size: 1em;
  margin-right: 10%;
}

#nav-bar ul li {
  list-style: none;
  margin: 2%;
}

#nav-bar ul a {
  text-decoration: none;
  color: #e8d5b5;
  position: relative;
  padding-bottom: 5px;
}

#nav-bar ul a:before {
  content: " ";
  border-bottom: solid 1px white;
  width: 0%;
  z-index: 200;
  height: 3px;
  bottom: 0;
  right: 0;
  position: absolute;
  transition: all 0.5s;
}

#nav-bar ul a:after {
  content: " ";
  border-bottom: solid 1px white;
  width: 0%;
  z-index: 200;
  height: 3px;
  bottom: 0;
  left: 0;
  position: absolute;
  transition: all 0.5s;
}

#nav-bar ul a:hover:after {
  width: 75%;
}

#nav-bar ul a:hover:before {
  width: 75%;
}

.basket--svg {
  position: absolute;
  cursor: pointer;
}

#burger {
  display: none;
}

#burger div {
  background-color: #e8d5b5;
  height: 3px;
  width: 26px;
  margin-bottom: 15%;
  transition: all ease 1s;
}

.open:nth-child(1) {
  transform: rotate(45deg) translateY(10px);
}

.open:nth-child(2) {
  opacity: 0;
}

.open:nth-child(3) {
  transform: rotate(-45deg) translateY(-10px);
}

.close:nth-child(1) {
  transform: rotate(0deg);
}

.close:nth-child(2) {
  opacity: 1;
}

.close:nth-child(3) {
  transform: rotate(0deg);
}

.main-container {
  margin: 20vh 5vh;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}

.main-container .main-container-text {
  width: 30vw;
  color: #e8d5b5;
}

.main-container .main-container-text h2 {
  font-size: 2em;
}

.main-container .main-container-text p {
