}

.btn {
  font-size: 0.9em;
  background-color: #005c8d;
  border: none;
  padding: 10px 20px;
  margin-top: 25px;
  font-weight: 800;
  border-radius: 10px;
  cursor: pointer;
  min-width: 150px;
  max-height: 60px;
  font-family: "Poppins", sans-serif;
  color: #e8d5b5;
}

.btn a {
  text-decoration: none;
  color: #e8d5b5;
}

.btn:hover {
  transform: scale(0.98);
}

