  }
}

.slide {
  transform: translateX(0%) !important;
  z-index: -1;
  transition: transform 2s;
}

.elements-slide {
  -webkit-animation: slidein 3s;
          animation: slidein 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes slidein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slidein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.cart {
  height: 500px;
  width: 300px;
